@font-face {
  font-family: opensans;
  src: url(../fonts/opensans.woff);
}
@font-face {
  font-family: opensans_bold;
  src: url(../fonts/opensansbold.woff);
}
@font-face {
  font-family: opensans_light;
  src: url(../fonts/opensanslight.woff);
}
@keyframes hand_anim {
  0% {
    transform: translateY(608px) rotateZ(40deg);
  }
  100% {
    transform: translateY(0px) rotateZ(0deg);
  }
}
.custom-button {
  border-radius: 50px;
  border-width: 3px;
  border-color: white;
  border-style: solid;
  background-color: rgba(0, 0, 0, 0);
  font-family: opensans_bold;
  color: white;
  font-size: 18px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0px;
  width: fit-content;
  transition: all, ease-in, 0.1s;
}
.custom-button:hover {
  background-color: #ffffff;
  color: #D22856;
}
.custom-button:active {
  background-color: #ffffff;
  color: #d2285563;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.object--hidden {
  display: contents;
}
.object--hidden:active {
  display: contents;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 50px) and (max-width: calc(320px - 1px)) {
  *:focus {
    outline: none;
  }
}
@media (min-width: 320px) and (max-width: calc(660px - 1px)) {
  *:focus {
    outline: none;
  }
}
.body {
  box-sizing: border-box;
  background-color: #d9d9d9;
  overflow-x: hidden;
  font-family: opensans;
  font-size: 14px;
  min-width: 220px;
}
.body::-webkit-scrollbar {
  width: 10px;
}
.body::-webkit-scrollbar-track {
  background-color: #757575;
}
.body::-webkit-scrollbar-thumb {
  background-color: #ffa228;
  border-radius: 50px;
  border-left: 1px;
  border-left-style: solid;
  border-left-color: black;
}
.link--os {
  line-height: 0;
  border: none;
}
.header {
