// содержит общие стили а также стили для body

*, *::before, *::after {
    box-sizing: inherit;
}

.object--hidden {                 // вырезает блок, оставляя его внутринности и настройки
    display: contents;

    &:active {
        display: contents;
    }
}

.visually-hidden {                // скрывает блок вместе с содержимим
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

//-------наведение табом---------------------------------------

.focus_mobile_mix() {
    outline: none;
}

// отулючить стили наведения для мобил
@media (min-width: @const_win_size_min) and (max-width: calc(@const_win_size_mobile - 1px)) {           // все что мение мобил
    *:focus {
        .focus_mobile_mix();
    }                             
}

@media (min-width: @const_win_size_mobile) and (max-width: calc(@const_win_size_tablet - 1px)) {        // для мобил
    *:focus {
        .focus_mobile_mix();  
    }                             
}
//----------------------------------------------

.body {
    box-sizing: border-box;
    background-color: @const_body_color;  
    overflow-x: hidden;
    font-family: opensans; 
    font-size: @font_def_size;
    min-width: @min_size_body;

    // cтиль скролла
    &::-webkit-scrollbar {      // весь скролл
        width: 10px;

        &-track {               // дорожка
            background-color: rgb(117, 117, 117);
        }

        &-thumb {               // плашка
            background-color: rgb(255, 162, 40);
            border-radius: 50px;
            border-left: 1px;
            border-left-style: solid;
            border-left-color: black;
        }
    }
}


.link {

    &--os {
        line-height: 0;
        border: none;
    }
}

